import { Component, OnInit  } from '@angular/core';
import { PicService } from '../pic.service';
import { Pics } from '../pics'


@Component({
  selector: 'app-first', 
  templateUrl: './first.component.html',
  styleUrls: ['../app.component.css']
})
export class FirstComponent implements OnInit {
  pics: any;
  title = 'pics';
  view: string;
  picUrl: any;


  constructor(private picService: PicService) {
    
    
  }

  ngOnInit() {
    this.view = "first";
    // this.picUrl = ['../assets/first/IMG_2801.JPG', '../assets/first/IMG_2802.JPG']
    // this.pics = this.picUrl
    this.getAllPics()
  }

  getAllPics(): void {

    let dir: string = this.view
    this.picService.getPics(dir).subscribe(pic => {
      this.pics = pic;
      console.log("this.pics: ", this.pics)
      let pics = [];
      this.pics.forEach(p => {
      pics.push(`../assets/${this.view}/${p}`)
      console.log("pics: ", pics, "this.picurl: ", this.picUrl)
      this.picUrl = pics;
      console.log("pics2: ", pics, "this.picurl2: ", this.picUrl)
      });
    })
  }

  toggleView(newView: string) {

    this.view = newView;
    this.getAllPics();
    
  }
}